import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import{Router} from "@angular/router"

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
name:String;
username:String;
email:String;
password:String;
reward:String;

  constructor(private validateService: ValidateService, private flashMessage:FlashMessagesService , private authservice:AuthService,
  private router:Router
  ) { }

  ngOnInit() {
  }
  onregistersubmit(){
  console.log("FUCKKKK")
  const user={
  name:this.name,
  email:this.email,
  username:this.username,
  password:this.password,
  reward:"100"
  }
  if(!this.validateService.validateRegister(user)){
  this.flashMessage.show("Please fill in all the fields",{cssClass: 'alert-danger', timeout:3000});
  return false;

  }

  if(!this.validateService.validateEmail(user.email)){
 this.flashMessage.show("Please use a valid email",{cssClass: 'alert-danger', timeout:3000});
  return false;
  }

  this.authservice.registerUser(user).subscribe(data=> {
  if (data.success){
  this.flashMessage.show("You are registered, and can Login",{cssClass: 'alert-success', timeout:3000})
  this.router.navigate(['/login'])
} else {
this.flashMessage.show("Something wrong",{cssClass: 'alert-danger', timeout:3000})
  this.router.navigate(['/register'])
  
}
  })

  }

}



