import { Injectable } from '@angular/core';
import {Http,Headers} from "@angular/http";
import "rxjs/add/operator/map";
import {tokenNotExpired} from 'angular2-jwt';

@Injectable()
export class AuthService {
	authtoken:any;
	user:any;
  public myuser:any
  public picture:any;
  
  constructor(private http:Http) { }

public getcoins(): any {
        return this.myuser;
    }

    public setcoins(scope: any): void {
        this.myuser = scope;
    }

  public setpicture(picturer:any): void{

    this.picture=picturer;
  }

  public getpicture(): any{

    return this.picture;
  }





 sendcoinstoserver(user){
 
 let headers = new Headers();
  headers.append('Content-Type','application/json');
  return this.http.post('users/updatereward',user,{headers:headers}).map(res=>res.json())
 

 }

  registerUser(user){

  let headers = new Headers();
  headers.append('Content-Type','application/json');
  return this.http.post('users/register',user,{headers:headers}).map(res=>res.json())
  }

  authenticateUser(user){

  let headers = new Headers();
  headers.append('Content-Type','application/json');

  return this.http.post('users/authenticate',user,{headers:headers}).map(res=>res.json())
  }

getProfile(){

let headers = new Headers();
this.loadToken();
  headers.append('Authorization',this.authtoken);
  headers.append('Content-Type','application/json');


  return this.http.get('users/profile',{headers:headers}).map(res=>res.json());
  }

  loadToken(){
  const token = localStorage.getItem('mytoken');
  this.authtoken = token;

}

  storeUserData(token,user){

  localStorage.setItem('mytoken',token);
  localStorage.setItem('user',JSON.stringify(user));
  this.authtoken=token;
  this.user=user;
  }
  logout(){
  this.authtoken = null;
  this.user = null;
  localStorage.clear();
  }

  isloggedin(){
  
  return tokenNotExpired('mytoken');
  }

  sendemail(user){

  let headers = new Headers();
  headers.append('Content-Type','application/json');
  console.log("senemail me ayaa")
  this.http.post('users/sendmail',user,{headers:headers})


  }
  
}









