import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router'
declare var display : any;
declare var display2 : any;
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
uploadFile: any;
hasBaseDropZoneOver: boolean = false;
options: any;
user:Object;
  constructor(private authservice:AuthService,
  private router : Router) { }

 
myfunchide():void{
  display2()
}
myfunc(): void{
display()
  
}

handleUpload(data): void {
    console.log("handle me ayaa")
    if (data && data.response) {
      data = JSON.parse(data.response);
      this.uploadFile = data.imagepath;
      location.reload();
          }
  }

  ngOnInit() {
  
  this.authservice.getProfile().subscribe(profile=>{ 
  console.log(profile.pathtofrontend)

  if(profile.pathtofrontend!=undefined){
  this.uploadFile = profile.pathtofrontend;
  }
  this.user = profile.user;
  this.authservice.setcoins(profile.user)
  this.authservice.setpicture(this.uploadFile)
  this.options = {
    url: 'http://localhost:4001/users/picture',
    data: {
        username: profile.user.username
      }
}; 

  },err=>{

  console.log(err)
  return false;
  })

  
  }



}



